import Smooth from "smooth-scrolling";
import { LocomotiveScrollProvider, useLocomotiveScroll } from "react-locomotive-scroll";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocomotiveScroll from "locomotive-scroll";

// import ScrollSmoother from "gsap/dist/ScrollSmoother";

// import { gsap } from "gsap-trial";
// import { ScrollTrigger } from "gsap-trial/dist/ScrollTrigger";
// import { ScrollSmoother } from "gsap-trial/dist//ScrollSmoother";
gsap.registerPlugin(ScrollTrigger);

function App() {
  const { scroll } = useLocomotiveScroll();

  const tl = gsap.timeline();
  const ref = useRef(null);
  const options = {
    el: ref.current,
    smooth: true,
  };
  // const scroll = new LocomotiveScroll({
  //   el: document.querySelector('.wrapper'),
  //   smooth: true,
  // });

  // const scroll = new LocomotiveScroll({ el: document.querySelectorAll("[data-scroll-container]"), smooth: true });

  useEffect(() => {
    const locoScroll = new LocomotiveScroll({
      el: document.body,
      smooth: true,
      getSpeed: true,
      getDirection: true,
      inertia: 0.55,
      useKeyboard: true,
      tablet: { smooth: true },
      smartphone: { smooth: true },
    });

    locoScroll.update();

    //console.log(ScrollTrigger.scrollerProxy)

    // each time Locomotive Scroll updates, tell ScrollTrigger to update too (sync positioning)
    locoScroll.on(document.body, ScrollTrigger.update);

    // tell ScrollTrigger to use these proxy methods for the ".smooth-scroll" element since Locomotive Scroll is hijacking things
    ScrollTrigger.scrollerProxy(document.body, {
      scrollTop(value) {
        return arguments.length ? locoScroll.scrollTo(value, 0, 0) : locoScroll.scroll.instance.scroll.y;
      }, // we don't have to define a scrollLeft because we're only scrolling vertically.
      getBoundingClientRect() {
        return {
          top: 0,
          left: 0,
          width: window.innerWidth,
          height: window.innerHeight,
        };
      },
      // LocomotiveScroll handles things completely differently on mobile devices - it doesn't even transform the container at all! So to get the correct behavior and avoid jitters, we should pin things with position: fixed on mobile. We sense it by checking to see if there's a transform applied to the container (the LocomotiveScroll-controlled element).
      pinType: document.body.style.transform ? "transform" : "fixed",
    });
    ScrollTrigger.addEventListener("refresh", () => locoScroll.update());

    // after everything is set up, refresh() ScrollTrigger and update LocomotiveScroll because padding may have been added for pinning, etc.
    ScrollTrigger.refresh();
    // setTimeout(() => {
    //   locoScroll.update();
    // }, 500);
  }, []);

  return (
    // <LocomotiveScrollProvider options={options} containerRef={ref}>
    <div className="wrapper scroll" data-scroll-section data-scroll-speed="-10">
      <div className="content">
        <header className="main-header">
          <div className="layers">
            <div
              className="layer__header"
              data-scroll
              data-scroll-speed="-2"
              // data-scroll //This attribute makes this section an independent scrollable container
              // data-scroll-speed="7"
              // data-scroll-section
            >
              <div className="layers__caption">Welcome to Parallax</div>
              <div className="layers__title">Fairy Forest</div>
            </div>

            <div className="layer layers__base" data-scroll data-scroll-speed="-3"></div>
            <div className="layer layers__middle"></div>
            <div className="layer layers__front"></div>

            {/* <section
                  className="layer layers__base js-parallax intro"
                  data-scroll //This attribute makes this section an independent scrollable container
                  data-scroll-speed="4"
                  data-scroll-section
                ></section>
                <section
                  className="layer layers__middle js-parallax intro"
                  data-scroll //This attribute makes this section an independent scrollable container
                  data-scroll-speed="4"
                  data-scroll-section
                ></section>
                <section
                  className="layer layers__front js-parallax intro"
                  data-scroll //This attribute makes this section an independent scrollable container
                  data-scroll-speed="4"
                  data-scroll-section
                ></section> */}
          </div>
        </header>

        <div className="main-article" data-scroll data-scroll-speed="1">
          <div className="main-article__content">
            <h2 className="main-article__header">To be continued</h2>
            <p className="main-article__paragraph">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Omnis error provident dignissimos facere. Repellendus tempore autem qui! Quia magnam tempora esse id necessitatibus corrupti mollitia expedita sapiente cum rerum, ut dicta laboriosam!</p>
          </div>
          <div className="copy">© WebDesign Master</div>
        </div>
      </div>
    </div>
    // </LocomotiveScrollProvider>
  );
}

export default App;
